import { Component, OnInit } from '@angular/core';
import { BasePage } from '../base/base.page';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { ModalController } from '@ionic/angular';
import { ContentFaqsModalComponent } from './content-faqs-modal/content-faqs-modal.component';

@Component({
  selector: 'app-faqs',
  templateUrl: './faqs.page.html',
  styleUrls: ['./faqs.page.scss'],
})
export class FaqsPage extends BasePage implements OnInit {
  public faqsOptionsMenu = 16;

  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    public modalController: ModalController
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {}

  public async goOption(option: number) {
    const modal = await this.modalController.create({
      component: ContentFaqsModalComponent,
      componentProps: {
        option,
      },
    });
    return await modal.present();
  }
}
