import { Component, Input, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { Constants } from '../../@global/constants';

@Component({
  selector: 'app-content-faqs-modal',
  templateUrl: './content-faqs-modal.component.html',
  styleUrls: ['./content-faqs-modal.component.scss'],
})
export class ContentFaqsModalComponent implements OnInit {
  public logoPath = Constants.LOGO_PATH;
  @Input() option: number;

  constructor(private modalController: ModalController) {}

  ngOnInit() {}

  public async goBack() {
    await this.modalController.dismiss();
  }
}
